/* Nav bar & banner */

header {
  color: white;
  background: -webkit-gradient(linear, left top, right top, from(rgba(0,0,0,.8)) , to(rgba(255,255,255,0.8)));
  background: -webkit-linear-gradient(left, rgba(0,0,0,.8) , rgba(255,255,255,0.8));
  background: -o-linear-gradient(left, rgba(0,0,0,.8) , rgba(255,255,255,0.8));
  background: linear-gradient(to right, rgba(0,0,0,.8) , rgba(255,255,255,0.8));
  max-height: 450px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
}

header img {
  width: 100%;
}

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(0, 0 , 0, .8);
  z-index: 5;
}

nav div {
  padding-right: 75px;
}

nav a {
  text-decoration: none;
  color: rgba(255, 255, 255, 1);
  padding: 10px;
  margin: 5px;
}

nav a:hover {
  background-color: rgba(255,255,255,0.9);
  color: rgba(0, 0 , 0, 0.9);
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

section {
  text-align: center;
  margin: 25px auto;
}

hr {
  width: 90%;
}

iframe {
  width: 560px;
  height: 315px;
}

#projects {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-auto-rows: minmax(100px, auto);
  grid-gap: 25px;
  z-index: 1;
}

.project-container {
    position: relative;
    width: 300px;
    height: 300px;
    border-radius: 10px;
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.proj-link {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.image {
  opacity: 1;
  display: block;
  width: 115%;
  height: auto;
  -webkit-transition: .5s ease;
  -o-transition: .5s ease;
  transition: .5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.text-wrapper {
  -webkit-transition: .5s ease;
  -o-transition: .5s ease;
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  width: 75%;
}

.project-container:hover .image {
  opacity: 0.3;
}

.project-container:hover .text-wrapper {
  opacity: 1;
}

.text-wrapper a {
  color: black;
}

.proj-header {
  grid-column-start: 2;
  grid-column-end: 4;
  grid-row: 1
}

.blog-header {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row: 1
}

.one {
  grid-column: 2;
  grid-row: 2
}

.two {
  grid-column: 3;
  grid-row: 2;
}

.three {
  grid-column: 2;
  grid-row: 3;
}

.four {
  grid-column: 3;
  grid-row: 3;
}

.five {
  grid-column: 2;
  grid-row: 4;
}

.six {
  grid-column: 3;
  grid-row: 4
}

#blog-feed {
  grid-column: 4;
  grid-row-start: 2;
  grid-row-end: 5;
  width: 400px;
  border-left: 1px solid gray;
  margin-left: 10px;
  padding-left: 10px;
}

.blog-post-list {
  list-style: none;
  text-align: left;
  margin: 0;
  padding: 0 20px;
}

.social-links {
  margin: 40px auto;
}

.social-links a {
  color: black;
  margin: 10px;
}

.social-links i:hover {
  -webkit-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
  -webkit-transition: 1s;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}


@media only screen and (max-width: 800px) {
  .about {
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .dev-blog, .project {
    margin: auto 25px;
  }
  iframe {
    max-width: 95%;
    max-height: 270px;
  }
}

@media only screen and (max-width: 480px) {
 .dev-blog, .project {
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .reverse-col {
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
  }
  .img-wrapper, .desc-text-wrapper {
    width: 95%;
    height: auto;
  }
}



